

.App {
  max-width: 600px;
  margin: 30px auto;

}

.App footer {
  font-size: 15px;
  margin: 10px 0;

}
.Weather {
    font-family: Arial, sans-serif;
    border: 1px solid #dadde1;
    padding: 16px;
    margin: 30px;
    border-radius: 4px;
    max-width: 600px;
    color: rgb(135, 135, 135);
}

.Weather form {
    margin-bottom: 20px;
}

.Weather h1 {
    font-size: 24px;
    font-weight: 100;
    margin: 20px 0 0;
    line-height: 29px;
}

.Weather ul {
    margin: 0;
    padding: 0;
}
.Weather li {
    font-size: 16px;
    font-weight: 100;
    line-height: 19px;
    list-style: none;
    
}

.Weather canvas {
    /* height: 64px;
    width: 64px; */
    margin-right: 5px;
    margin-top: 5px;
    float: left;
    /* display: inline-block; */
    /* position: relative;
    top: -10px; */

}

.Weather .temperature {
    font-weight: bold;
    color: rgb(33, 33, 33);
    font-size: 60px;
    line-height: 64px;
    font-weight: 400;
    
    
}

.Weather .unit {
    color: rgb(33, 33, 33);
    font-size: 16px;
    font-weight: 400;
    position: relative;
    top: -34px;
    line-height: 1;
}

.WeatherForecast {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.WeatherForecast-day {
    position: relative;
    display: flex;
    margin-left: 6px;
}
.WeatherForecat-temperature-min {
    opacity: 0.6;
    margin-left: 5px;
}
.WeatherForecat-temperature-max {
    margin-left: -5px;
}
